.LogisticsContainer {
  padding: 3rem;
  background-color: var(--primary-color);
}

.HeaderContainer {
  text-align: center;
  color: var(--light-color);
}

.LogisticsHeader {
  font-size: 2rem;
  padding: 1rem 0;
}
.LogisticsSecondaryHeader {
  color: #ccc;
}

.LogisticsStepsContainer {
  padding: 3rem;
}
.LogisticsGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  color: var(--light-color);
}
.LogisticsStep {
  text-align: center;
}
.StepIcon {
  padding: 1rem;
  margin-bottom: 2rem;
  color: var(--fourth-color);
}
.LogisticsStepHeader {
  font-size: 22px;
}
.LogisticsStepText {
  padding: 1.5rem 2rem;
  line-height: 1.6;
}

@media (max-width: 950px) {
  .LogisticsGrid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .LogisticsStep {
    margin: 2rem 0;
  }
}

@media (max-width: 490px) {
  .LogisticsStepsContainer {
    padding: 0;
  }
}
